.service{
  &-inner{
    padding-top: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &-item{
   width: 49%;
   @include mq{
     width: 100%;
    &:nth-of-type(n+2){
      margin-top: 25px;
    } 
   }
   img{
     width: 100%;
   }
  }
     &-tarea{
       padding-top: 8px;
     }
 &-text{
   @include fz(16);
 }
 &-btn{
   @include _btn;
   &:hover{
     @include _hover;
   }
 }
}