.company{
  display: flex;
&-left{
  width: 50%;
  background-color: $rColor;
  color: white;
  text-align: center;
  @include fz(24);
  font-weight: bold;
  a{
   display: inline-block;
    width: 100%;
    line-height: 10;
    
  }
  &:hover{
    @include _hover;
  }
}
&-right{
  width: 50%;
  background-color: $bColor;
  color: white;
  text-align: center;
  @include fz(24);
  font-weight: bold;
  a{
    width: 100%;
   display: inline-block;
   line-height: 10;
  }
  &:hover{
    @include _hover;
  }
}
}